import React from 'react';
import { Bar } from 'react-chartjs-2';

const MetricsGraphs = ({ selectedArea, selectedSubArea, selectedYear, workflowData }) => {
  // Example data for the chart
  const data = {
    labels: workflowData.map((item) => item.id),
    datasets: [
      {
        label: `Workflows in ${selectedArea} - ${selectedSubArea} (${selectedYear})`,
        data: workflowData.map((item) => item.workflow.length), // Example: Count of workflows
        backgroundColor: 'rgba(75, 192, 192, 0.6)',
        borderColor: 'rgba(75, 192, 192, 1)',
        borderWidth: 1,
      },
    ],
  };

  return (
    <div>
      <h2>Metrics for {selectedArea} - {selectedSubArea} ({selectedYear})</h2>
      <Bar data={data} />
    </div>
  );
};

export default MetricsGraphs;
